* {
  margin: 0;
  padding: 0;
}

body {
  font-family: '맑은 고딕', Arial, sans-serif;
  font-size: 16px;
  color: #333;
}

.header {
  background: blue;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
}

.header h1 {
  color: #fff;
  font-size: 30px;
}

.gnb {
  position: fixed;
  top: 0;
  right: -100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 2000;
  width: 100%;
  height: 100%;
  transition: 0.5s;
}

.gnb .close_btn {
  border: none;
  position: absolute;
  top: 0;
  right: 10px;
  z-index: 20001;
  color: #333;
  padding: 5px 10px;
  cursor: pointer;
  background: none;
}

.gnb ul {
  overflow-y: auto;
  position: absolute;
  top: 0;
  right: 0;
  width: 400px;
  height: 100%;
  padding: 60px 20px;
  background: #fff;
  box-sizing: border-box;
}

.gnb li {
  list-style: none;
  padding: 8px 5px;
  border-bottom: 1px solid #ccc;
  transition: 0.3s;
}

.gnb li button {
  display: block;
  width: 100%;
  height: 100%;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
}

.gnb li:hover {
  background: #ccc;
}

main {
  padding: 30px 10px;
}

.list01 {
  margin: 10px 0 10px 30px;
}

main .title2 {
  margin: 14px 0;
  padding: 3px 0;
  background-color: #e42f2f;
  color: #fff;
}

main .title3 {
  margin: 10px 0;
  padding: 3px 0;
  background-color: #d27575;
  color: #fff;
}

main .title4 {
  display: inline-block;
  margin: 10px 0;
  padding: 3px 10px;
  background-color: #eacbcb;
}

.footer {
  text-align: center;
  line-height: 40px;
  background: #333;
}

.footer address {
  color: #fff;
  font-style: normal;
}