.product {
  width: 1200px;
  margin: auto;
  padding: 20px 0;
}

.product-title {
  margin-bottom: 20px;
  font-size: 18px;
}

.product-list {
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.product-list li {
  width: 24%;
}

.product-list li a {
  text-decoration: none;
  color: #333;
}

.product-list li img {
  width: 100%;
  margin-bottom: 10px;
}

.product-list li h4 {
  margin-bottom: 10px;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.6;
  overflow: hidden;
  text-overflow: ellipsis;
  /* white-space: nowrap; */
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical
}

.product-list li p {
  font-size: 14px;
}

.product-list li strong {
  color: red;
  font-size: 16px;
  font-weight: bold;
}