.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.modal-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
}

.modal-inner img {
  display: block;
  width: 100%;
}

.modal-inner button {
  display: block;
  width: 100%;
  padding: 10px;
  background: #fff;
  border: none;
  box-sizing: border-box;
  cursor: pointer;
}