.success {
  background: green;
}

.failure {
  background: orange;
}

.formStyle {
  margin: 15px;
}

.formStyle fieldset {
  padding: 14px;
}

.formStyle legend {}

.formStyle p {
  line-height: 50px;
}

.formStyle input {
  border: 1px solid #ccc;
  width: 288px;
  height: 40px;
}

.formStyle input[type="button"] {
  background: #a12e2e;
  border: none;
  color: #fff;
  width: 140px;
  margin-right: 10px;
}