.txt_color1 {
  color: green;
}

#bg_color1 {
  color: blue;
}

.bg_img {
  width: 840px;
  height: 160px;
  background: url('../images/banner2.png');
}

.num {
  color: red;
  font-weight: bold;
  font-size: 30px;
}

.btn {
  width: 40px;
  height: 30px;
  font-size: 20px;
  margin: 10px;
}

.kurly-cart {
  width: 500px;
  padding: 10px;
  background: #f8f8f8;
  box-sizing: border-box;
}

.kurly-cart * {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.6;
  color: #333;
  vertical-align: baseline;
  box-sizing: border-box;
}

.kurly-cart .count {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 30px;
  padding: 20px;
  border: 1px solid #ccc;
}

.kurly-cart .count h3 {
  width: 100%;
  margin-bottom: 20px;
}

.kurly-cart .num {
  display: inline-flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.kurly-cart .num input {
  padding: 0 10px;
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

.kurly-cart .price .origin {
  text-decoration: line-through;
  color: #aaa;
}

.kurly-cart .price .discount {
  font-weight: bold;
}

.kurly-cart .total-price {
  margin-bottom: 30px;
  text-align: right;
}

.kurly-cart .total-price span {
  font-size: 20px;
  font-weight: bold;
}

.kurly-cart .total-price strong {
  font-size: 40px;
  font-weight: bold;
}

.kurly-cart .btn-cart {
  display: block;
  width: 100%;
  padding: 20px 10px;
  background: purple;
  border: none;
  border-radius: 5px;
  color: #fff;
}